            <div>区域</div>
            <div>企业名称</div>
            <div>街道</div>
            <div>运维周期</div>
            <div>详情</div>
            <!-- <div style="width:20px;"></div> -->
          </div>
          <div v-for="(item, index) in list" :key="index">
            <div slot="header">
              <div class="flex1" @click="show2=!show2">
                <div>
                  {{item.area}}
                  <!-- <span class="run" v-if="(item.status==0)">待运维</span>
                  <span class="run1" v-if="(item.status==1)">运维完成</span> -->
                </div>
                <div>{{item.restaurant}}</div>
                <div>{{item.street}}</div>
                
                <!-- <div style="width:30vw">{{item.createtime}}</div> -->
                <div>
                  {{item.period}}
                  <span style="font-size:5px;color:orange">天</span>
                </div>
                <div @click="look(item.dutyid)">
                  <a>查看</a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </mu-container>
    </div>
    <div id="bottomFooter">
      <mu-container
        style="max-width: 100vw; width:100vw;"
        id="bottomFooter"
        v-for="(item,index) in list1"
        :key="index"
      >
        <mu-bottom-nav :value.sync="shift" @change="change">
          <mu-bottom-nav-item
            value="0"
            title="总览"
            icon="language"
            to="/Home"
            v-show="menuArr.HomeStatus"
          ></mu-bottom-nav-item>
          <mu-bottom-nav-item
            value="1"
            title="日常管理"
            icon="event"
            to="/DayControl"
            v-show="menuArr.DayStatus"
          ></mu-bottom-nav-item>
          <mu-bottom-nav-item
            value="2"
            title="运维管理"
            icon="build"
            to="/Control"
            v-show="menuArr.RunControlStatus"
          ></mu-bottom-nav-item>
          <mu-bottom-nav-item
            value="3"
            title="设置"
            icon="settings"
            to="/Settings"
            v-show="menuArr.systemStatus"
          ></mu-bottom-nav-item>
        </mu-bottom-nav>
