</template>
<script>
var token = sessionStorage.getItem("token");
// import MyFooter from "../myFooter";
// var token = sessionStorage.getItem("token");
// import AppBace from '../../../static/ajax.js'
export default {
  data() {
    return {
      // languages:[],
      labelPosition: "top",
      form: {
        input: "",
        select: "",
        date: "",
        radio: "",
        checkbox: [],
        switch: false,
        slider: 30,
        textarea: ""
      },
      color1: "",
      color2: "",
      color3: "",
      color4: "",
      color5: "",
      open: false,
      panel: "",
      show2: false,
      loading: "",
      show1: true,
      show: false,
      area: null,

      street: null,
      restaurantcat: null,
      statusValue: null,
      starttime: null,
      lasttime: null,
      timer: false,
      value5: null,
      value6: null,
      menu2: [],
      Menu: [],
      user: "",
      menuArr: [
        {
          HomeStatus: false,
          DayStatus: false,
          RunControlStatus: false,
          systemStatus: false,
          newAdd: false
        }
      ],
      list8: [],
      count: 5,
      currentPage: 1,
      ID: 0,
      people: "",
      openSimple: false,
      openSimple1: false,
      openFullscreen: false,
      index: 0,
      list: [],
      list5: [],
      list2: [],
      list3: [],
      list4: [],
      min: true,
      number: 0,
      name: "",
      id: 0,
      token: sessionStorage.getItem("token"),
      msg: "hello 数据详情1111",
      shift: "2",
      list1: [{ name: "总览", icon: "language" }],
      custom: {
        value1: null,
        value2: null,
        value3: null,
        value4: null,
        value11: null
      },
      languages: ["VM000001", "VM000002"],
      languages1: ["福田区"],
      languages2: ["梅林街道", "园岭街道"],
      languages3: ["湘菜", "粤菜", "川菜"]
    };
  },
  methods: {
    // 筛选
    closeBottomSheet() {
      this.open = false;
    },
    openBotttomSheet() {
      this.open = true;
    },
    // 加载的动画
    fullscreen() {
      this.loading = this.$loading();
    },
    controlStatus() {
      this.color4 = "background: rgb(82, 145, 228);color:white";
      this.color5 = "";
      this.statusValue = 1;
      console.log(this.statusValue);
      // debugger
    },
    controlStatus1() {
      this.color4 = "";
      this.color5 = "background: rgb(82, 145, 228);color:white";
      this.statusValue = 0;
    },
    loop() {
      this.color1 = "";
      this.color2 = "";
      this.color3 = "";
      this.color4 = "";
      this.color5 = "";
      this.open = false;
      this.starttime = null;
      this.lasttime = null;
      this.value5 = null;
      this.value6 = null;
      this.custom.value1 = null;
      this.custom.value11 = null;
      this.custom.value2 = null;
      this.custom.value3 = null;
      this.custom.value4 = null;
      this.area = null;
      this.street = null;
      this.statusValue = null;
      this.restaurantcat = null;
      this.axios();
      this.timer = false;
    },
    toggle(panel) {
      this.panel = panel === this.panel ? "" : panel;
    },
    screen1() {
      this.color1 = "background: rgb(82, 145, 228);color:white";
      this.color2 = "";
      this.color3 = "";
      this.timer = true;
      const d = new Date();
      const resDate = this.filter(d);
      console.log("resDate", resDate);
      this.value6 = resDate;
      const start = new Date();
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 1);
      console.log("start", start);
      const resDate1 = this.filter(start);
      this.value5 = resDate1;
      console.log("resdate", resDate1);
    },
    screen2() {
      this.color1 = "";
      this.color2 = "background: rgb(82, 145, 228);color:white";
      this.color3 = "";
      this.timer = true;
      const d = new Date();
      const resDate = this.filter(d);
      this.value6 = resDate;
      console.log("resDate", resDate);
      const start = new Date();
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 3);
      console.log("start", start);
      const resDate1 = this.filter(start);
      this.value5 = resDate1;
      console.log("resdate", resDate1);
      // this.query(resDate, resDate1);
    },
    screen3() {
      this.color1 = "";
      this.color2 = "";
      this.color3 = "background: rgb(82, 145, 228);color:white";
      this.timer = true;

      const d = new Date();
      const resDate = this.filter(d);
      this.value6 = resDate;
      console.log("a", resDate);
      const start = new Date();
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
      console.log("start", start);
      const resDate1 = this.filter(start);
      console.log("resdate", resDate1);
      this.value5 = resDate1;
    },
    query(e, f) {
      this.open = false;
      if (this.timer) {
        const resDate1 = this.filter(f);
        console.log("resDate1", resDate1);
        this.status = this.custom.value11;
        this.area = this.custom.value2;
        this.street = this.custom.value3;
        this.restaurantcat = this.custom.value4;
        this.starttime = resDate1;
        this.lasttime = e;
        this.axios();
        this.timer = false;
      } else {
        if (this.value5 == undefined || this.value6 == undefined) {
          this.starttime = null;
          this.lasttime = null;
          this.area = this.custom.value2;
          this.status = this.custom.value11;
          this.street = this.custom.value3;
          this.restaurantcat = this.custom.value4;
          this.axios();
        } else {
          console.log("value5", this.value5);
          const resDate = this.filter(this.value5);
          console.log("value5", resDate);
          const e = new Date(this.value6);
          const resDate1 = this.filter(this.value6);
          console.log(resDate1);

          this.area = this.custom.value2;
          this.status = this.custom.value11;
          this.street = this.custom.value3;
          this.restaurantcat = this.custom.value4;
          this.starttime = resDate;
          this.lasttime = resDate1;
          console.log("this.value6", this.street);
          this.axios();
        }
      }
    },
    filter(e) {
      const d = new Date(e);
      return (
        d.getFullYear() +
        "-" +
        this.p(d.getMonth() + 1) +
        "-" +
        this.p(d.getDate())
      );
    },
    p(s) {
      return s < 10 ? "0" + s : s;
    },
    look(id) {
      this.$router.push({ path: "/controlDetails", query: { id: id } });
    },
    menu() {
      var m = [];
      var menu1 = sessionStorage.getItem("user");
      m = menu1.split(",");
      console.log("m", m);
      this.menu2.push(m);
      console.log("user111", this.menu2);
      for (let index = 0; index < this.menu2.length; index++) {
        this.Menu = this.menu2[index];
        console.log("32", this.Menu);
      }
      var a = [];
      a = this.Menu;
      if (
        (a.indexOf("新增") != -1 &&
          (this.user == "超级管理员" ||
            this.user == "运维人员" ||
            this.user == "管理员")) ||
        this.user == "企业人员"
      ) {
        this.menuArr.newAdd = true;
      } else {
        this.menuArr.newAdd = false;
      }
      this.menuArr.HomeStatus = sessionStorage.getItem("HomeStatus");
      if (this.menuArr.HomeStatus == "false") {
        this.menuArr.HomeStatus = false;
      } else {
        this.menuArr.HomeStatus = true;
      }
      // console.log(this.menuArr.HomeStatus);
      this.menuArr.DayStatus = sessionStorage.getItem("DayStatus");
      if (this.menuArr.DayStatus == "false") {
        this.menuArr.DayStatus = false;
      } else {
        this.menuArr.DayStatus = true;
      }
      this.menuArr.RunControlStatus = sessionStorage.getItem(
        "RunControlStatus"
      );
      if (this.menuArr.RunControlStatus == "false") {
        this.menuArr.RunControlStatus = false;
      } else {
        this.menuArr.RunControlStatus = true;
      }

      this.menuArr.systemStatus = sessionStorage.getItem("systemStatus");
      if (this.menuArr.systemStatus == "false") {
        this.menuArr.systemStatus = false;
      } else {
        this.menuArr.systemStatus = true;
      }
    },
    initNormal() {
      var echarts = require("echarts");
      let myChart = echarts.init(document.getElementById("main1"));
      myChart.setOption({
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        color: ["rgb(18, 187, 238)", "#ccc"],
        series: [
          {
            name: "访问来源",
            type: "pie",
            radius: ["50%", "80%"],

            avoidLabelOverlap: false,
            label: {
              // color:"#ccc",
              normal: {
                show: true,
                position: "center",
                fontSize: "40",
                color: "#000"
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "40",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [{ value: 600, name: "60%" }, { value: 400, name: "" }]
          }
        ]
      });
    },
    init(e) {
      var echarts = require("echarts");
      let myChart = echarts.init(document.getElementById("main"));
      myChart.setOption({
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        color: ["rgb(231, 141, 227)", "#ccc"],
        // legend: {
        //   orient: "vertical",
        //   x: "center",
        //   data: ["完成率"]
        // },
        series: [
          {
            name: "访问来源",
            type: "pie",
            radius: ["50%", "80%"],

            avoidLabelOverlap: false,
            label: {
              // color:"#ccc",
              normal: {
                show: true,
                position: "center",
                fontSize: "40",
                color: "#000"
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "40",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [{ value: 60, name: "15%" }, { value: 340, name: "" }]
          }
        ]
      });
    },
    add() {
      this.$router.replace("/Add");
    },
    showList() {
      console.log(1);
    },
    change(e) {
      console.log(e);
    },
    async axios() {
      var url = "http://47.112.16.168:8800/operate/query";
      // var url = "http://192.168.50.151:8800/operate/query";
      var that = this;
      var areaname = this.area;
      var streetname = this.street;

      var device = this.custom.value11;
      console.log(device, "device");
      var type = this.restaurantcat;
      var startTime = this.value5;
      var endTime = this.value6;
      console.log("startTime", streetname, "endTime", endTime);
      await this.$http({
        method: "post",
        url: url,
        headers: {
          token: that.token
        },
        data: {
          areaname,
          streetname,
          device,
          type,
          startTime,
          endTime
        }
      })
        .then(res => {
          console.log(res, "hrllo");
          that.loading.close();
          that.list = res.data.data;
          console.log(that.list);
          that.normalRate = res.data.normalRate;
          that.finishRate = res.data.finishRate;
          that.init();
          that.initNormal();
          // debugger
          that.list2 = res.data.area;
          that.list3 = res.data.status;
          that.list4 = res.data.street;
          that.list5 = res.data.foodType;
          console.log("list1", that.list2);
        })
        .catch(function(error) {
          console.log("出错了", error);
        });
    }
  },
  async created() {
    this.fullscreen();
    this.user = sessionStorage.getItem("lastname");
    await this.menu();
    await this.axios();
    await console.log(sessionStorage.getItem("navItem"));
  },
  async mounted() {
    this.AppBase.date();
  }
};
</script>
